<template>
  <div class="advisory-pop">
    <div class="content popup-animat">
      <h3>咨询方向</h3>
      <div class="close" @click="closePopup"></div>

      <select v-model="fromInfo.advisoryType">
        <option v-for="(name, k) in typeList" :key="k" > {{name}} </option>
      </select>

      <p>留下您的联系方式，我们会及时对您的问题进行一对一的解答！</p>
      <input type="text" v-model="fromInfo.advisoryName" placeholder="您的称呼（必填）" />
      <i v-if="fromError.advisoryName">请输入您的称呼。</i>

      <input type="text" v-model="fromInfo.advisoryContact" placeholder="您的联系方式（必填）" />
      <i v-if="fromError.advisoryContact">请输入您的联系方式。</i>

      <input type="text" v-model="fromInfo.advisoryEmail" placeholder="您的邮箱地址（必填）" />
      <i v-if="fromError.advisoryEmail">请输入您的邮箱地址。</i>
      <button @click="submitForm">
        <template v-if="submitLoad">
          <Loading :btnLogin="true" />
        </template>
        <template v-else>确定</template>
      </button>
    </div>
    <SubmitSuccess v-if="submitStatus" @close="submitStatus = false" />

  </div>
</template>
<script>
import SubmitSuccess from '../base/SubmitSuccess'
import Loading from './Loading'
export default {
  components: {
    SubmitSuccess,
    Loading
  },
  props: {
    page: String,
    type: String,
    proTitle: String
  },
  data () {
    return {
      typeList: [
        '购买新楼盘',
        '二手公寓',
        '出售房源',
        '租房',
        '商业地产买卖',
        '银行借贷',
        '其他'
      ],
      fromInfo: {
        advisoryType: '购买新楼盘',
        advisoryName: '',
        advisoryContact: '',
        advisoryEmail: ''
      },
      fromError: {
        advisoryType: '',
        advisoryName: '',
        advisoryContact: '',
        advisoryEmail: ''
      },
      submitStatus: false,
      submitLoad: false
    }
  },
  methods: {
    closePopup () {
      this.$emit('closePopuo')
    },
    selectType (type) {
      this.fromInfo.advisoryType = type
    },
    submitForm () {
      for (const info in this.fromInfo) {
        if (this.fromInfo[info] === '') {
          this.fromError[info] = true
          return false
        } else {
          this.fromError[info] = false
        }
      }
      const params = {
        email_title: '',
        type: this.fromInfo.advisoryType,
        name: this.fromInfo.advisoryName,
        contact: this.fromInfo.advisoryContact,
        email: this.fromInfo.advisoryEmail
      }
      if (this.page === 'home') {
        params.email_title = '房产咨询'
      }
      if (this.page === 'product_details') {
        params.email_title = '新楼盘咨询'
        params.advisory_source = this.proTitle
      }
      if (this.page === 'estate') {
        params.email_title = '商业地产咨询'
        params.advisory_source = this.proTitle
      }
      if (this.submitLoad) return
      this.submitLoad = true
      this.$httpApi.messageApi(params).then(res => {
        if (res.code === 200) {
          this.submitLoad = false
          this.submitStatus = true
          for (const info in this.fromInfo) {
            this.fromInfo[info] = ''
          }
        }
      })
    }
  },
  mounted () {
    if (this.type) {
      this.selectType (this.type)
    }
  }
}
</script>
